document.addEventListener('DOMContentLoaded', function () {

$('.sidebar__item > a').click(function(){
  $('.sidebar__link--active').removeClass('sidebar__link--active');
  $(this).addClass('sidebar__link--active');
  //* открывает подменю по всей ссылке
  if($(this).hasClass('withsub')){
    $(this).next('.sidebar__submenu').slideToggle();
    $(this).find('.sidebar__link-icon--arrow').toggleClass('active');
  } ;
});

//* открывает подменю только по стрелке
// $('.sidebar__link-icon--arrow').click(function(){
//   $('.sidebar__submenu').slideToggle();
//   $(this).toggleClass('active');
// });

$('.sidebar__submenu > li > a').click(function(){
  $('.sidebar__link--active').removeClass('sidebar__link--active');
  $(this).addClass('sidebar__link--active');
  $('.sidebar__submenu').prev('a').addClass('sidebar__link--active');

});


//* table selector
$(document).ready(function() {
  $('.table-header__select').select2({
    placeholder: 'Формат',
    width: 170,
    minimumResultsForSearch: -1,
  });
});


//table sort
$(document).ready(function(){
  $(".table").tablesorter({
    cssHeader: 'table__head'
  });
});


//* burger
const burger = document.querySelector('.burger')
const mainMenu = document.querySelector('.menu__list')
const body = document.querySelector('body')

burger.classList.add('unToggled')

document.addEventListener('click', function(e){
   if(e.target == burger){
    burger.classList.toggle('toggled')
    burger.classList.toggle('unToggled')
    mainMenu.classList.toggle('active')
    if (burger.classList.contains('toggled')){
      body.classList.add('hidden')
    } else {
      body.classList.remove('hidden')
    }
   } else if(e.target !== burger && 
    e.target !== mainMenu.classList.contains('active') && 
    e.target !== document.querySelector('.menu__link--more')) {
    (e.target !== burger && 
      e.target !== mainMenu.classList.contains('active') && 
      e.target !== document.querySelector('.menu__link--more'))
       burger.classList.remove('toggled')
       burger.classList.add('unToggled')
       mainMenu.classList.remove('active')
       body.classList.remove('hidden')
   }
});


//* switch badges
function switchBadges(){
  const badges = document.querySelector('.mh-badges')
  const mHeader = document.querySelector('.main__header')
  const sideBar = document.querySelector('.sidebar')
  

  if (window.innerWidth <= 768){
    $('.sidebar__submenu').slideUp();
    $('.sidebar__link-icon--arrow').addClass('active');
    sideBar.insertAdjacentElement('beforeend', badges)
    if (window.innerWidth <= 630){
      mHeader.insertAdjacentElement('beforeend', badges)
    } else {
      sideBar.insertAdjacentElement('beforeend', badges)
    }
  } else {
    $('.sidebar__submenu').slideDown();
    $('.sidebar__link-icon--arrow').removeClass('active');
    mHeader.insertAdjacentElement('afterbegin', badges)
  }
}

switchBadges()

window.addEventListener('resize', function() {
  switchBadges()
})


















})